body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.OuterWrapper {
	width: 80%;
	margin: auto;
	position: relative;
	opacity: 1.0;
	transition: opacity 500ms 500ms;
}


#cloud-one {
	position: absolute;
	top: 40%;
	left: 5%;
	z-index: 1;

}

#cloud-two {
	position: absolute;
	top: 30%;
	right: 5%;
	z-index: 1;
	-webkit-transform: scale(1.5);
	        transform: scale(1.5);
}

@media only screen and (max-width: 600px) {
  .OuterWrapper {
    width: 100%;
  }

  #cloud-one {
  	left: -15%;
  	top: 30%;
  }

  #cloud-two {
  	right: -25%;
  	top: 15%
  }
}




.MainContainer {
  display: block;
  position: relative;
  margin: auto;
  width: 100%;
  border-radius: 3px;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
  background-size: 100%;
  background-repeat: no-repeat;
}

#dallas{
  width: 100%;
  margin-bottom:-3px;
}

.MainWeather {
  display: inline-block;
  position: absolute;
  width: 50%;
  top: 0;
  left:0;
}


.temperature {
  display: inline-block;
}
.temperature h1 {
  display: inline-block;
  margin: 9px 0 0 28px;
  height: 65px;
  font-family: 'Open Sans';
  font-size: 48px;
  font-weight: normal;
  color: #65aed5;
}

.degree {
  display: inline-block;
  width: 6px;
  height: 6px;
  border: solid 2.5px #65aed5;
  border-radius: 50%;
  margin-bottom: 18px;
}

.icon {
  display: inline-block;
  margin-left: 5px;
  position: relative;
  top: 8px;
}

.details {
  display: inline-block;
  position: relative;
  top: 5px;
}

.details h2 {
  margin: 0 0 0 12px;
  display: block;
  font-family: OpenSans;
  font-size: 14px;
  font-weight: 600;
  color: #65aed5;
}


.WeatherToggle {
  position: absolute;
  padding-top: 5%;
  right: 0;
  display: inline-block;
  width: 50%;
  text-align: right;
}


.WeatherDisplay {
  display:block;
  background-color: white;
  width: 100%; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
  box-sizing: border-box;  
}

.forecastContainer {
  display:inline-block;
  width: 20%;
  text-align: center;
  border-right: solid 1px #d8d8d8; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
  box-sizing: border-box;  
}

.forecastContainer h1 {
  font-family: 'Open Sans';
  font-size: 14px;
  font-weight: bold;
  color: #4a4a4a;
}

.forecastContainer .weatherIcon img {
  height: 38px;
}

.forecastContainer p {
  display: inline-block;
  margin: 5px 0 20px 0;
  font-family: 'Open Sans';
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  
}

.tempHigh{
  font-size: 24px;
  color: #4a4a4a;
  padding-right: 7px;
}

.tempLow {
  font-size: 18px;
  color: #7c7c7c;
}


@media only screen and (max-width: 600px) {
  .MainContainer {
    box-shadow: none;
    background-color: rgba(0, 0, 0, 0);
    height: 80vh;
    text-align: center;
  }

  .WeatherToggle {
    top: -160px;
    right: 5px;
  }

  #dallas {
    display: none;
  }

  .MainWeather {
    display: block;
    width: 100%;
  }

  .temperature {
    display: block;
  }

  .degree {
    position: absolute;
    display: block;
    width: 7px;
    height: 7px;
    top: 150px;
    left: 225px;
  }

  .temperature h1 {
    font-size: 60px;
    font-weight: 600;
    color: #65aed5;
    position: absolute;
    right: 0;
    left: 0;
    margin: 0 0 0 -25px;
    top: 120px;
  }

  .details {
    display: block;
    position: absolute;
    text-align: center;
    top: 220px;
    right: 0;
    left: 0;
    margin: 0 auto;
  }

  .details h2 {
    margin: 0;
  }

  .icon {
    display: block;
    position: absolute;
    top: 20px;
    right: 0;
    left: 0;
    margin: 0 auto;
  }

  .WeatherDisplay {
    padding-top: 300px;
    background-color: rgba(0,0,0,0);
  }

  .forecastContainer {
    background-color: white;
    box-shadow: 0 3px 2px -1px rgba(0, 0, 0, 0.2);
  }
}

body {
  background-image: linear-gradient(50deg, #115da3, #4ecfed);
  background-size: cover;
  padding: 8%; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
  box-sizing: border-box;
  height: 100vh;  
}


.InnerWrapper {
  width: 70%;
  margin: auto;
}


.DisplayInfo {
  display: block;
  margin: auto;
  text-align: center;
  width: 100%;
  padding: 0 0 3% 0;
  font-family: 'Open Sans';
  color: white;
}

.DisplayInfo img {
  display: inline-block;
  width: 12px;
  margin-right: 6px;
  margin-bottom: -1px;
}

.DisplayInfo h1 {
  display: inline-block;
  letter-spacing: 0.1px;
  margin: 0;
  height: 24px;
  font-size: 18px;
  font-weight: 600;

}

.DisplayInfo h2 {
  margin: 0;
  height: 19px;
  font-size: 14px;
  font-weight: normal;
}


@media only screen and (max-width: 600px) {
  body {
    background-image: linear-gradient(25deg, #d9fbff, #ffffff);
    padding: 0;
  }

  .InnerWrapper {
    width: 100%;
  }

  .DisplayInfo h1 {
    color: #1768aa;
    margin-top: 30%;
  }

  .DisplayInfo h2 {
    color: #65aed5;
  }
}

.pulse {
    -webkit-animation: pulse 2s;
            animation: pulse 2s;
}

@-webkit-keyframes pulse {
    0%,
    25%,
    50%,
    75%,
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
        -webkit-transform: translateY(-12px);
                transform: translateY(-12px);
    }
    60% {
        -webkit-transform: translateY(-7px);
                transform: translateY(-7px);
    }
}

@keyframes pulse {
    0%,
    25%,
    50%,
    75%,
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
        -webkit-transform: translateY(-12px);
                transform: translateY(-12px);
    }
    60% {
        -webkit-transform: translateY(-7px);
                transform: translateY(-7px);
    }
}


@media only screen and (max-width: 600px) {
  .icon-size {
      width: 110px;
    }
}
.toggle {

}

.switch {
  margin-right: 20px;
  position: relative;
  display: inline-block;
  width: 55px;
  height: 25px;
  border-radius: 25px;
  background-color: #2685bd;
  z-index: 0;
}

.switch h1 {
  display: inline;
  position: absolute;
  left: 7px;
  top: -7.5px;
  font-size: 14px;
  font-family: 'Open Sans';
  font-weight: 600;
  z-index: 1;
}

.switch-right h1 {
  color: white;
}

.switch-left h1 {
  color: #2685bd;
}

.switch-right h2 {
  color: #2685bd;
}

.switch-left h2 {
  color: white;
}

.switch h2 {
  display: inline;
  position: absolute;
  right: 3.5px;
  top: -9px;
  font-size: 14px;
  font-family: 'Open Sans';
  font-weight: 600;
  z-index: 1;
}

.toggle-button {
  height: 21px;
  width: 21px;
  border-radius: 50%;
  background-color: white;
  z-index: 0;
}

.toggle-button-right {
  position: absolute;
  right: 7px;
  bottom: 2.2px;
  -webkit-transform: translateX(5px);
  transform: translateX(5px);
  transition: .4s;
}

.toggle-button-left {
  position: absolute;
  left: 7px;
  bottom: 2.2px;
  transition: .4s;
  -webkit-transform: translateX(-5px);
  transform: translateX(-5px);
}



