.pulse {
    animation: pulse 2s;
}

@keyframes pulse {
    0%,
    25%,
    50%,
    75%,
    100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-12px);
    }
    60% {
        transform: translateY(-7px);
    }
}


@media only screen and (max-width: 600px) {
  .icon-size {
      width: 110px;
    }
}